/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useCallback, useEffect, useState } from "react";
import { MenuItem } from "@material-ui/core";
import {
  Button,
  Checkbox,
  FormControlLabel,
  FormGroup,
  Hidden,
  Link,
} from "@mui/material";
// components imports
import { CustomTextFieldFilled } from "../../../../../Components/UI/CustomTextField/CustomTextField";
import { UIButton } from "../../../../../Components/exports";
import FileAttachment from "../../../../../Components/UI/FileAttachment";
import { useToastMessage } from "../../../../../contexts/ToastMessageContext";

// api imports
import {
  fetchProfileInfo,
  fetchBusinessProfileInfo,
  updateWABusinessProfile,
  fetchAPIProvider,
} from "../../../../../api/WABusinessSettings";
import { get_wa_number } from "../../../../../api/WhatsApp";
import inactiveWA from "../../../../../assets/inactive-wa.svg";

import BotImg from "../assets/bot.svg";
import MsgImg from "../assets/messages.svg";

// style imports
import useStyles from "../styles/muiStyles";

const DesktopSettings = ({ waStatus }) => {
  const allowedVertical: { label: string; value: string }[] = [
    { label: "Automotive", value: "AUTO" },
    { label: "Beauty, Spa and Salon", value: "BEAUTY" },
    { label: "Clothing and Apparel", value: "APPAREL" },
    { label: "Education", value: "EDU" },
    { label: "Entertainment", value: "ENTERTAIN" },
    { label: "Event Planning and Service", value: "EVENT_PLAN" },
    { label: "Finance and Banking", value: "FINANCE" },
    { label: "Food and Grocery", value: "GROCERY" },
    { label: "Public Service", value: "GOVT" },
    { label: "Hotel and Lodging", value: "HOTEL" },
    { label: "Medical and Health", value: "HEALTH" },
    { label: "Non-profit", value: "NONPROFIT" },
    { label: "Not a business", value: "NOT_A_BIZ" },
    { label: "Professional Services", value: "PROF_SERVICES" },
    { label: "Shopping and Retail", value: "RETAIL" },
    { label: "Travel and Transportation", value: "TRAVEL" },
    { label: "Restaurant", value: "RESTAURANT" },
    { label: "Other", value: "OTHER" },
  ];
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const classes = useStyles();
  const [address, setAddress] = useState<string>("");
  const [about, setAbout] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [vertical, setVertical] = useState<string | null>(null);
  const [, setWebsites] = useState<string>("");
  const [website1, setWebsite1] = useState<string | null>(null);
  const [website2, setWebsite2] = useState<string | null>(null);
  const [photoLink, setPhotoLink] = useState<string | null>(null);
  const [uploadPhoto, setUploadPhoto] = useState<any | null>(null);
  const [loading, setLoading] = useState<any>(true);
  const [showCheckToFill, setShowCheckToFill] = useState<any>(true);
  const [invalidWebsiteURL, setInvalidWebsiteURL] = useState<any>(false);
  const [isGupShUp, setIsGupShUp] = useState<boolean>(true);
  const [showGSSuccess, setShowGSSuccess] = useState<boolean>(false);
  const [updateBtnLabel, setUpdateBtnLabel] = useState<string>("Submit");
  const [sendInfoRequest, setSendInfoRequest] = useState<boolean>(false);

  const [number, setNumber] = useState<string>("");
  // check if URL is valid
  const isValidURL = (string) => {
    var res = string.match(/https?:\/\//g);
    return res !== null;
  };
  // validateURL
  const validateURL = (value) => {
    setInvalidWebsiteURL(value !== "" ? !isValidURL(value) : false);
  };

  const resetFields = () => {
    setAddress("");
    setAbout("");
    setDescription("");
    setEmail("");
    setVertical(null);
    setWebsites("");
    setWebsite1(null);
    setWebsite2(null);
    setPhotoLink(null);
    setUploadPhoto(null);
    setShowCheckToFill(true);
    setInvalidWebsiteURL(false);
    setIsGupShUp(true);
    setShowGSSuccess(false);
  };

  // Event handlers
  const businessProfileUpdateForm = async (event) => {
    try {
      event.preventDefault();
      const formData = new FormData();
      if (uploadPhoto) {
        console.log(uploadPhoto);
        formData.append("file", uploadPhoto);
      }
      formData.append("description", description);
      formData.append("email", email);
      formData.append("address", address);
      formData.append("about", about);
      formData.append("vertical", vertical);
      if (website1 && website1.length) {
        if (website1.includes("http://") || website1.includes("https://")) {
          formData.append("websites[]", website1);
        } else {
          return setErrorMessage(
            "Please add valid protocol prefixes in website 1!"
          );
        }
      }
      if (website2 && website2.length) {
        if (website2.includes("http://") || website2.includes("https://")) {
          formData.append("websites[]", website2);
        } else {
          return setErrorMessage(
            "Please add valid protocol prefixes in website 2!"
          );
        }
      }
      // const {
      //   aboutSuccess,
      //   photoSuccess,
      //   businessProfileSuccess,
      //   gupshupEmailBody,
      //   isProviderGupshup,
      //   updateRequestSent,
      // } = await updateWABusinessProfile(formData);

      const res = await updateWABusinessProfile(formData);
      if(res.errors){
        setErrorMessage(res.errors[0].message);
      }

      // if (gupshupEmailBody && isProviderGupshup && updateRequestSent) {
      //   // TODO: Hide the update info form and show success message
      //   setShowGSSuccess(true);
      // }

      // if (aboutSuccess || photoSuccess || businessProfileSuccess) {
      res && res.success && setSuccessMessage("Updated business profile info!");
      // }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };

  const fetchWaNumber = useCallback(async () => {
    try {
      const response = await get_wa_number();
      if (!response.success) {
        throw Error(response.message);
      }
      if (response.payload.length > 0) {
        setNumber(response.payload[0].id);
      }
    } catch (error) {
      setErrorMessage(error.message);
    }
  }, []);

  useEffect(() => {
    waStatus && fetchWaNumber();
  }, [waStatus]);

  useEffect(() => {
    if (waStatus && !isGupShUp) {
      setUpdateBtnLabel("Submit");
      setSendInfoRequest(true);
    }
  }, [waStatus, isGupShUp]);

  useEffect(() => {
    if (!waStatus && !sendInfoRequest) {
      setUpdateBtnLabel("Update Business Information");

      fetchBusinessProfileInfo()
        .then((res) => {
          if (!res.success) {
            return setErrorMessage(res.message);
          }
          const {
            description,
            email,
            address,
            about,
            websites,
            vertical,
            profile_picture_url,
          } = res.payload;
          res.success && setShowCheckToFill(false);
          if (websites) {
            const [website1, website2] = websites;
            if (website1) {
              setWebsite1(website1);
            }
            if (website2) {
              setWebsite2(website2);
            }
            setWebsites(websites.join(", "));
          }
          setEmail(email || "");
          setDescription(description || "");
          setAddress(address || "");
          setAbout(about || "");
          setVertical(vertical || "");
          setPhotoLink(profile_picture_url);
        })
        .catch((error) => {
          setErrorMessage(error.message);
        });
      // if (uploadPhoto) {
      //   const photoURL = URL.createObjectURL(uploadPhoto);
      //   setPhotoLink(photoURL);
      // }
    }
    // eslint-disable-next-line
  }, [waStatus, sendInfoRequest]);

  const handleOkayClick = () => {
    setShowGSSuccess(false);
    resetFields();
  };

  return (
    <Fragment>
      {/* remove && false later */}
      {!waStatus && false ? (
        <div className={classes["form-stacked-cards"]}>
          <div className="card-group inactive">
            <img src={inactiveWA} alt="inactive-wa" />
            <div className="info">
              <p className="desc">
                {
                  "Your WhatsApp Business API account is not active yet. It usually takes upto 72 business hours for activation."
                }
              </p>
              <p className="desc">
                {"Please contact us on "}
                <Link href="mailto:support@easysocial.io" underline="hover">
                  {"support@easysocial.io"}
                </Link>
                {" in case it is taking longer than expected."}
              </p>
            </div>
          </div>
        </div>
      ) : showGSSuccess ? (
        <div className={classes["form-stacked-cards"]}>
          <div className="card-group">
            <h2 className="card-title">Success</h2>
            <p>
              We've received your email for the business profile update request.
              We'll update your profile in approximately 2 business days.
            </p>
            <UIButton className="mt-4 ms-0" onClick={handleOkayClick}>
              <span className="text-limiter">{"Okay"}</span>
            </UIButton>
          </div>
        </div>
      ) : (
        <form
          className={classes["form-stacked-cards"]}
          onSubmit={businessProfileUpdateForm}
          noValidate
        >
          <div className="card-group">
            <h2 className="card-title">{"Business Information"}</h2>
            {!loading && showCheckToFill && (
              <div className="flex-row check-to-fill">
                <div className="centered-colum">
                  <FormGroup>
                    <FormControlLabel
                      control={<Checkbox />}
                      label="Fill Information from Available Business Details"
                    />
                  </FormGroup>
                </div>
              </div>
            )}
            <div className="flex-row">
              <div className="grow-colum">
                <div
                  className="field"
                  style={{ marginBottom: 0, height: "100%" }}
                >
                  <CustomTextFieldFilled
                    multiline
                    value={description}
                    id="description"
                    name="description"
                    label="Business Description"
                    placeholder="Business Description"
                    rows={6}
                    inputProps={{ maxLength: 256 }}
                    onChange={(e) => setDescription(e.target.value)}
                    style={{ height: "100%" }}
                  />
                </div>
              </div>
              <div className="shrink-colum">
                <FileAttachment
                  name="file"
                  value={
                    uploadPhoto
                      ? URL.createObjectURL(uploadPhoto)
                      : photoLink
                      ? photoLink
                      : null
                  }
                  inputChangedHandler={(e) => setUploadPhoto(e.target.files[0])}
                  removeAttachment={() => {
                    console.log("Remove Attachment Callback");
                  }}
                  allowRemove={false}
                  required="false"
                  title="Select Profile Picture"
                  width={"157px"}
                  height={"157px"}
                />
              </div>
            </div>
            <div className="flex-row">
              <div className="grow-colum">
                <div className="field space-top">
                  <CustomTextFieldFilled
                    select
                    label="Business Vertical"
                    placeholder="Business Vertical"
                    InputLabelProps={{ shrink: vertical !== null }}
                    value={vertical ? vertical : ""}
                    variant="filled"
                    type="select"
                    inputProps={{ "aria-label": "Without label" }}
                    fullWidth
                    name="vertical"
                    onChange={(e) => setVertical(e.target.value)}
                    className={classes["selectButtonWrapper"]}
                  >
                    <MenuItem
                      value="UNDEFINED"
                      selected={vertical ? true : false}
                    >
                      Select vertical
                    </MenuItem>
                    {allowedVertical.map(({ label, value }) => {
                      return (
                        <MenuItem key={value} value={value}>
                          {label}
                        </MenuItem>
                      );
                    })}
                  </CustomTextFieldFilled>
                </div>
                <div className="field">
                  <CustomTextFieldFilled
                    type="text"
                    name="about"
                    label="About"
                    value={about}
                    onChange={(e) => setAbout(e.target.value)}
                    id="about"
                    placeholder="About"
                    inputProps={{ maxLength: 256 }}
                    required={true}
                  />
                </div>
                <div className="field">
                  <CustomTextFieldFilled
                    type="text"
                    name="address"
                    label="Address"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    id="address"
                    placeholder="Address"
                    inputProps={{ maxLength: 256 }}
                  />
                </div>

                <div className="field">
                  <CustomTextFieldFilled
                    type="email"
                    name="email"
                    label="Email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    id="email"
                    placeholder="email"
                  />
                </div>
              </div>
            </div>
            {invalidWebsiteURL && (
              <div className="flex-row">
                <div className="grow-colum">
                  <small className="d-block mb-2">
                    {
                      "Prefixes http:// or https:// required before websites url"
                    }
                  </small>
                </div>
              </div>
            )}
            <div className="flex-row min-gap">
              <div className="half">
                <div className="field">
                  <CustomTextFieldFilled
                    type="text"
                    name="website1"
                    label="Website 1"
                    InputLabelProps={{ shrink: website1 !== null }}
                    value={website1 ? website1 : ""}
                    onChange={(e) => {
                      setWebsite1(e.target.value);
                      validateURL(e.target.value);
                    }}
                    id="website1"
                    inputProps={{ maxLength: 256 }}
                    placeholder="Website 1"
                  />
                </div>
              </div>
              <div className="half">
                <div className="field">
                  <CustomTextFieldFilled
                    type="text"
                    name="website2"
                    label="Website 2"
                    InputLabelProps={{ shrink: website2 !== null }}
                    value={website2 ? website2 : ""}
                    onChange={(e) => {
                      setWebsite2(e.target.value);
                      validateURL(e.target.value);
                    }}
                    id="website2"
                    inputProps={{ maxLength: 256 }}
                    placeholder="Website 2"
                  />
                </div>
              </div>
            </div>
            <div className="action">
              <UIButton className="mt-4 ms-0" type="submit">
                <span className="text-limiter">{updateBtnLabel}</span>
              </UIButton>
            </div>
          </div>
          <div className="chatbot-card">
            <img src={BotImg} alt="bot" className="bot-image" />
            <div className="bot-info">
              <h3 className="quote">
                {
                  "Start building your WhatsApp Chatbot in seconds. Use the advanced "
                }
                <strong>{"Chatflow"}</strong>
                {
                  " tool by EasySocial, and build complex conversations in a jiffy."
                }
              </h3>
              <div className="bot-actions">
                <Button variant="contained" href={`/chat-flow/${number}`}>
                  <span>{"Build Chatbot"}</span>
                  <svg
                    width="14"
                    height="10"
                    viewBox="0 0 14 10"
                    fill="none"
                    xmlns="//www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M11.3692 4.19223L8.28352 1.00205L9.25275 0L14 4.90798L9.43077 10L8.46154 8.99796L11.3692 5.62372H0V4.19223H11.3692Z"
                      fill="#12B0EE"
                    />
                  </svg>
                </Button>
                <Hidden mdDown implementation="js" only="md">
                  <img src={MsgImg} alt="msg  " />
                </Hidden>
              </div>
            </div>
          </div>
        </form>
      )}
    </Fragment>
  );
};

export default DesktopSettings;
