/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment, useCallback, useEffect, useState } from "react";
import { Hidden } from "@mui/material";

import { get_wa_Status } from "../../../api/WhatsApp";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import MobileSettings from "../../../Container/BusinessSettings/Whatsapp/settings/Mobile";
import DesktopSettings from "../../../Container/BusinessSettings/Whatsapp/settings/Desktop";

import Layout from "../Layout";

const WABusinessProfileSettings = (props) => {
  const [waStatus, setWaStatus] = useState();
  const { setErrorMessage } = useToastMessage();
  const getWaStatus = useCallback(async () => {
    try {
      const result = await get_wa_Status();
      setWaStatus(result.success);
    } catch (error) {
      setErrorMessage(error.message);
    }
  }, []);

  useEffect(() => {
    getWaStatus();
  }, [getWaStatus]);

  return (
    <Fragment>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopSettings waStatus={waStatus} />
        </Layout>
      </Hidden>
      <Hidden smUp implementation="js">
        <MobileSettings waStatus={waStatus} />
      </Hidden>
    </Fragment>
  );
};

export default WABusinessProfileSettings;
