const fetchProfileInfo = (): Promise<any> =>
  new Promise(async (resolve, reject) => {
    try {
      const url: string = `${BE_URL}/wa/profile`;

      const fetchProfileRes = await axios({
        url,
        headers: {
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      const { error, message, about, photoLink } = fetchProfileRes.data;

      if (error) {
        return reject({ message: error.message });
      }

      if (message) {
        return reject({ message });
      }

      return resolve({ about, photoLink });
    } catch (error: any) {
      return reject({ message: error.message });
    }
  });

const fetchBusinessProfileInfo = async () => {
  try {
    const url: string = `${API_V2}/whatsapp/business-profile`;

    const fetchBusinessProfileRes = await fetch(url, {
      headers: AuthenticatedHeaders(),
    });
    const data = await fetchBusinessProfileRes.json();
    if (!fetchBusinessProfileRes.ok) {
      throw Error(data.message);
    }
    return data;
  } catch (error: any) {
    return { success: false, message: error.message };
  }
};

const updateWAProfile = (formData): Promise<any> =>
  new Promise(async (resolve, reject) => {
    try {
      const url: string = `${BE_URL}/wa/profile/update`;

      const updateProfileRes = await axios({
        url,
        method: "POST",
        data: formData,
        headers: {
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          "Content-Type": "application/json",
        },
      });

      const { error, message, aboutSuccess, photoSuccess } =
        updateProfileRes.data;

      if (error) {
        return reject({ message: error.message });
      }

      if (message) {
        return reject({ message });
      }

      return resolve({ aboutSuccess, photoSuccess });
    } catch (error: any) {
      return reject({ message: error.message });
    }
  });

const updateBusinessProfile = (profileData): Promise<any> =>
  new Promise(async (resolve, reject) => {
    try {
      const url: string = `${API_V2}/whatsapp/profile-update`;

      const updateBusinessProfileRes = await axios({
        url,
        method: "POST",
        data: profileData,
        headers: {
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          "Content-Type": "application/json",
        },
      });

      const { error, message, data } = updateBusinessProfileRes.data;

      if (error) {
        return reject({ message: error.message });
      }

      if (message) {
        return reject({ message });
      }

      return resolve({ data });
    } catch (error: any) {
      return reject({ message: error.message });
    }
  });

const updateWABusinessProfile = async (formData) => {
  try {
    const url: string = `${API_V2}/whatsapp/profile-update`;

    const updateWABusinessProfileRes = await fetch(url, {
      method: "POST",
      headers: {
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
      body: formData,
    });

    return await updateWABusinessProfileRes.json();
  } catch (error: any) {
    return { message: error.message };
  }
};

const fetchAPIProvider = async (): Promise<string> => {
  try {
    const url: string = `${BE_URL}/wa/provider`;

    const updateWABusinessProfileRes = await axios({
      url,
      headers: {
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "Content-Type": "multipart/form-data",
      },
    });

    const { provider } = updateWABusinessProfileRes.data;

    return provider;
  } catch (error: any) {
    throw new Error(error.message || "Something went wrong");
  }
};

export {
  fetchProfileInfo,
  fetchBusinessProfileInfo,
  updateWAProfile,
  updateBusinessProfile,
  updateWABusinessProfile,
  fetchAPIProvider,
};
